      <option value="russian-federation">russian-federation</option>
      <option value="serbia">serbia</option>
      <option value="slovakia">slovakia</option>
      <option value="slovenia">slovenia</option>
      <option value="spain">spain</option>
      <option value="sweden">sweden</option>
      <option value="switzerland">switzerland</option>
      <option value="turkey">turkey</option>
      <option value="ukraine">ukraine</option>
      <option value="united-kingdom">united-kingdom</option>
    </select>
    <m-article v-if="products.length !== 0">
      <button class="edit" @click="editThis()">
        <router-link to="/update">
          <i class="fas fa-edit"></i>
        </router-link>
      </button>
      <button class="delete" @click="deleteThis()">
        <i class="fas fa-trash-alt"></i>
      </button>

      <div class="images">
        <img
          v-for="img in products[i].images"
          :key="img.id"
          :src="url + 'images/' + img.filename"
          alt="house photos"
        />
      </div>

      <div class="flexy">
        <div class="infos">
          <h3>{{ products[i].title }}</h3>
          <p>{{ products[i].details }}</p>
        </div>

        <div class="contact">
          <h3>Contact informations :</h3>
          <div v-for="(contact, app) in contactInfos" :key="contact">
            <div v-if="contact !== ''">
              <b>{{ app }}</b
              >: {{ contact }}
            </div>
          </div>
        </div>
      </div>

      <div class="tags">
        <span class="tag" v-for="tag in productTags" :key="tag">
          <span>{{ tag }}</span>
        </span>
      </div>
    </m-article>
    <transition name="fade-button" mode="out-in">
      <button @click="switchPages('back')" v-if="i >= 1">Previous</button>
    </transition>
    <transition name="fade-button" mode="out-in">
      <button @click="switchPages('next')" v-if="i < products.length - 1">
        Next
      </button>
    </transition>
  </div>
</template>

<script>
import axios from "axios";
import MArticle from "../components/MArticle";

export default {
  name: "GET",
  components: { MArticle },
  data() {
    return {
      url: this.$store.state.url,
      i: 0,
      products: [{ title: "", details: "", tags: "", contact: {}, images: [] }],
    };
  },
  computed: {
    productTags() {
      return this.products[this.i].tags.split(" ");
    },
    contactInfos() {
      return this.products[this.i].contact;
    },
    currentCountry() {
      return this.$store.state.currentCountry;
    },
  },
  methods: {
    setCountry(e) {
      this.$store.commit("changeCountry", e.currentTarget.value);
      this.getProducts();
    },
    switchPages(where) {
      where === "back" ? this.i-- : this.i++;
    },
    getProducts() {
      axios
        .get(this.url + this.currentCountry)
        .then((res) => {
          this.products = res.data;
          console.log(res.data);
          this.productTags = res.data[this.i].tags.split(" ");
          this.contactInfos = res.data[this.i].contact;
        })
        .catch((err) => console.log(err));
    },
    deleteThis() {
      axios
        .delete(this.url + this.products[this.i]._id)
        .then((res) => {
          console.log("deleted");
          console.log(res);
        })
        .catch((err) => console.log(err));

      this.getProducts();
    },
    editThis() {
      this.$store.state.editedNow = this.products[this.i];
      this.deleteThis();
      document.querySelector(".edit a").click();
    },
  },
  mounted() {
    this.getProducts();
  },
};
</script>

