<template>
  <div>
    <form
      :action="url + 'updateItem'"
      method="PUT"
      enctype="multipart/form-data"
    >
      <div>
        <fieldset>
          <legend>About the product</legend>
          <label for="title">Title</label>
          <input type="text" name="title" id="title" :value="editedNow.title" />
          <label for="details">Body</label>
          <input
            type="text"
            name="details"
            id="details"
            :value="editedNow.details"
          />
          <label for="country">Country</label>
          <input
            type="text"
            name="country"
            id="country"
            :value="editedNow.country"
          />
          <label for="address">Address</label>
          <input
            type="text"
            name="address"
            id="address"
            :value="editedNow.address"
          />
          <label for="tags">Tags</label>
          <input type="text" name="tags" id="tags" :value="editedNow.tags" />
          <label for="images">Images</label>
          <input type="file" name="images" id="images" multiple />
        </fieldset>

        <fieldset>
          <legend>Contact Infos</legend>
          <label for="phone">phone</label>
          <input
            type="number"
            name="phone"
            id="phone"
            :value="editedNow.phone"
          />
          <label for="whatsapp">whatsapp</label>
          <input
            type="number"
            name="whatsapp"
            id="whatsapp"
            :value="editedNow.whatsapp"
          />
          <label for="telegram">telegram</label>
          <input
            type="number"
            name="telegram"
            id="telegram"
            :value="editedNow.telegram"
          />
          <label for="mail">mail</label>
          <input type="email" name="mail" id="mail" :value="editedNow.mail" />
        </fieldset>
      </div>
      <input
        type="hidden"
        name="productId"
        id="productId"
        :value="editedNow"
        placeholder="id of the updated product"
      />
      <button id="update-submit" @click.prevent="putReq()"></button>
    </form>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "UPDATE",
  computed: {
    url() {
      return this.$store.state.url;
    },
    editedNow() {
      return this.$store.state.editedNow;
    },
  },
  methods: {
    putReq() {
      let title = document.getElementById("title").value;
      let country = document.getElementById("country").value;
      let details = document.getElementById("details").value;
      let address = document.getElementById("address").value;
      let tags = document.getElementById("tags").value;
      let images = document.getElementById("images").value;
      let phone = document.getElementById("phone").value;
      let whatsapp = document.getElementById("whatsapp").value;
      let telegram = document.getElementById("telegram").value;
      let mail = document.getElementById("mail").value;
      let productId = document.getElementById("productId");

      axios.put(this.url + "updateItem/" + productId.value, {
        title: title,
        country: country,
        details: details,
        address: address,
        tags: tags,
        images: images,
        phone: phone,
        whatsapp: whatsapp,
        telegram: telegram,
        mail: mail,
      });
    },
  },
};
</script>

<style lang="scss" scoped>
form {
  width: 100%;
  margin: 40px auto;
  text-align: center;
  input[type="submit"] {
    margin: 10px;
  }
  div {
    text-align: left;
    width: 100%;
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    label,
    input {
      display: block;
      margin: 10px 0;
    }
  }
}
</style>
